.headerBox {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 10px;
    flex-flow: row wrap;

    button {

        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 32px;
        font-family: 'Avenir';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
        /* identical to box height, or 117% */



        &:nth-of-type(1) {
            background: #E0E0E0;
            color: #333;
        }

        &:nth-of-type(2) {
            background: transparent;
            color: #095B2C;
            border: 1px solid #27AE60;
        }

        svg {
            margin: 0;
        }
    }

    @media screen and (max-width:768px) {
        justify-content: flex-start;
    }
}

.transfertitle {
    font-family: 'Avenir';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;

    color: #333333;
}